              <div class="card-body">
                <h4 class="mb-0 text-white">Búsqueda de Expedientes</h4>
              </div>
            </div>
          </div>
        </div> -->
      <div class="row">
        <div class="col-xl-12">
          <div class="card shadow">
            <div class="card-body">
              <div class="table-responsive">
                <table class="table table-bordered" id="miTabla">
                  <thead>
                    <tr>
                      <th>N° de Expediente</th>
                      <th>Fecha de <br> Inicio</th>
                      <th>Demandante</th>
                      <th>Demandado</th>
                      <th>Pretensión</th>
                      <!-- <th>Materia</th> -->
                      <th>Estado</th>
                      <th>Monto de <br> Pretencion</th>
                      <th>Monto de <br> Ejecucion</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>5670-2017-0-1708-JM-LA-01</td>
                      <td>2023-10-11</td>
                      <td>Flores Montero Ana Maria</td>
                      <td>Unprg</td>
                      <td>Beneficios Sociales</td>
                      <td>
                        <span :class="badgeClass('En Ejecucion')">
                          En Ejecucion
                        </span>
                      </td>
                      <td>$ 10000</td>
                      <td>$ 5000</td>
                    </tr>
                    <tr>
                      <td colspan="6"></td>
                      <td>Total a Pagar</td>
                      <td>$ 5000</td>
                    </tr>
                    <!-- <tr v-for="expediente in expedientes" :key="expediente.exp_id">
                        <td class="col-md-2">
                          <router-link :to="{ name: 'expediente/detalle', params: { id: expediente.exp_id } }">
                            {{ expediente.numero }}
                          </router-link>
                        </td>
                        <td>{{ expediente.fecha_inicio }}</td>
                        <td>{{ getPersonaName(expediente) }}</td>
                        <td>Unprg</td>
                        <td>{{ expediente.pretencion }}</td> -->
                    <!-- <td>{{ expediente.materia }}</td> -->
                    <!-- <td>
                          <span :class="badgeClass(expediente.estado_proceso)">
                            {{ expediente.estado_proceso }}
