                        <td>12456</td>
                       <td>10000</td>
                      </tr> -->
                  </tbody>
                </table>
              </div>
              <!-- <div class="row">
                    <div class="col-md-12">
                        <tr>
                            <td colspan="6"></td>
                            <td>Monto de Pago Total</td>
                            <td>$ 234567</td>
                        </tr>
                    </div>
                </div> -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
  
<script>
import feather from 'feather-icons';
import $ from 'jquery';
import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import 'datatables.net-buttons-bs5';
import 'datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css';
import { mapActions } from 'vuex';

export default {
  name: 'Buscar',

  created() {
    this.cargarexp();
  },

  mounted() {
    this.$nextTick(() => {
      feather.replace();
    });
  },

  data() {
    return {
      expedientes: [],
      dataTable: null,
    };
  },

  methods: {
    ...mapActions(['get', 'post']),

    cargarexp() {
      this.get({
        url: this.$store.getters.get__url + '/proceeding',
        token: this.$store.getters.get__token,
      })
        .then((response) => {
          console.log(response);
          this.expedientes = response.data;
          this.initDataTable();
        })
        .catch((errors) => { });
    },

    initDataTable() {
      if (this.dataTable) {
        this.dataTable.destroy();
      }

      this.$nextTick(() => {
        this.dataTable = $('#miTabla').DataTable({
          language: {
            url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
          },
        });
        feather.replace();
      });
    },

    badgeClass(estado) {
      return {
        'badge tramite p-2 rounded': estado === 'En Tramite',
        'badge ejecucion p-2 rounded': estado === 'En Ejecucion',
      };
    },

    getPersonaName(expediente) {
      if (expediente.tipo_persona === 'natural') {
        return `${expediente.apellido_paterno} ${expediente.apellido_materno} ${expediente.nombres}`;
      } else {
        return expediente.razon_social;
      }
    },
  },

  beforeDestroy() {
    if (this.dataTable) {
      this.dataTable.destroy();
    }
  },
};
</script>
  
<style scoped>
td {
  white-space: normal !important;
  word-wrap: break-word !important;
}

.tramite {
  background-color: #fff6e5;
  color: #F4BD50;
  border: 1px solid #F4BD50;
}

.ejecucion {
  background-color: #EAFEF3;
  color: #2ecc71;
  border: 1px solid #2ecc71;
}

.badge {
  font-size: 13px !important;
}
</style>
  